header {
  background-color: rgb(29, 29, 29);
  align-items: center;
  display: flex;
  justify-content: space-between;
  padding: 5px 25px;
}

.title-header-link {
  color: white;
  text-decoration: none;
}

.header-link {
  color: white;
  margin: 5px;
  text-decoration: none;
}

body {
  background-image: url("https://images4.alphacoders.com/839/83944.jpg");
  background-size: cover;
  color: white;
}

.profile-pic {
  border-radius: 5px;
  box-shadow: rgba(204, 204, 204, 0.2) 0px 7px 29px 0px;
  float: left;
  margin: 15px;
  width: 350px;
}

.main-home {
  display: flex;
  height: 100%;
  margin-top: 8%;
  justify-content: center;
}

.presentation-text {
  align-items: center;
  display: flex;
  margin-left: 15px;
}

.presentation-text h2 {
  font-size: 20px;
  font-weight: 400;
}

.trybe-logo {
  margin-left: 5px;
  width: 80px;
}

.about-container {
  margin: 50px;
}

.skills-container {
  display: flex;
  justify-content: space-evenly;
  margin-top: 50px;
}

.skills {
  list-style: none;
  padding: 0;
}

.contact-logo {
  border-radius: 10px;
  margin: 20px;
  padding: 3px;
  height: 55px;
}

.contact-container {
  text-align: center;
}

fieldset {
  height: 470px;
}

.form-container {
  background-color: rgb(29, 29, 29);
  border-radius: 5px;
  box-shadow: rgba(204, 204, 204, 0.2) 0px 7px 29px 0px;
  display: flex;
  flex-flow: column;
  margin: 50px auto;
  padding: 25px;
  width: 50%;
}

.email-form-title {
  margin-bottom: 50px;
}

.projects-container {
  display: flex;
  justify-content: space-evenly;
  flex-flow: row wrap;
}

.card-item {
  margin: 5px;
}

.project-title {
  margin: 15px;
}

.card {
  background-color: rgb(29, 29, 29);
  color: white;
}

@media screen and (max-width: 600px) {
  header {
    flex-direction: column;
    width: auto;
  }

  body {
    background-color: rgb(29, 29, 29);
    background-image: url('https://images.unsplash.com/photo-1597415884397-6c1213ee6d8e?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxzZWFyY2h8OHx8Z3JleSUyMHRleHR1cmV8ZW58MHx8MHx8&w=1000&q=80');
    background-repeat: no-repeat;
    background-size: cover;
  }

  .main-home {
    flex-direction: column;
    padding: 25px;
  }

  .presentation-text {
    margin-top: 20%;
  }

  .presentation-text h2 {
    font-size: 25px;
    line-height: 35px;
  }

  .profile-pic {
    float: none;
    margin: 25px auto;
    width: 60%;
  }

  .skills-container {
    flex-direction: column;
  }

  .skills {
    margin-top: 35px;
  }

  .form-container {
    margin: 25px auto;
    width: 80%;
  }
}